// components/forms/TransactionForm.tsx
"use client";
import { useState } from 'react';
import { useFinance } from '@/context/FinanceContext';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import { Label } from '@/components/ui/label';
import { Switch } from '@/components/ui/switch';
import { Tabs, TabsList, TabsTrigger } from '@/components/ui/tabs';
import { Save, ArrowRightLeft } from 'lucide-react';
import { v4 as uuidv4 } from 'uuid';

// 1. AQUÍ ESTÁ LA CLAVE: Definimos que el componente acepta 'defaultType'
interface Props {
  onClose?: () => void;
  defaultType?: 'income' | 'expense' | 'transfer';
}

// 2. Recibimos 'defaultType' con un valor por defecto
export default function TransactionForm({ onClose, defaultType = 'expense' }: Props) {
  const { addTransaction, accounts, categories } = useFinance();
  
  // 3. Usamos 'defaultType' para iniciar el estado del Tab
  const [mode, setMode] = useState<'income' | 'expense' | 'transfer'>(defaultType);
  
  // Estados Generales
  const [amount, setAmount] = useState('');
  const [desc, setDesc] = useState('');
  const [date, setDate] = useState(new Date().toISOString().split('T')[0]);
  const [accountId, setAccountId] = useState('');
  
  // Estados Específicos Gasto/Ingreso
  const [categoryId, setCategoryId] = useState('');
  const [isFixed, setIsFixed] = useState(false);
  
  // Estados Lógica Ahorro
  const [isSavings, setIsSavings] = useState(false);
  const [didWithdraw, setDidWithdraw] = useState(true); // ¿Se retira el dinero?

  // Estados Transferencia
  const [targetAccountId, setTargetAccountId] = useState('');

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    if (!amount || !accountId) return;

    const numAmount = parseFloat(amount);
    // Forzamos mediodía para evitar problemas de zona horaria
    const safeDate = new Date(`${date}T12:00:00`).toISOString();

    if (mode === 'transfer') {
        if (!targetAccountId) return;
        const originAcc = accounts.find(a => a.id === accountId);
        const targetAcc = accounts.find(a => a.id === targetAccountId);

        addTransaction({
            type: 'transfer',
            amount: numAmount,
            accountId, // Origen
            relatedAccountId: targetAccountId, // Destino
            originAccountName: originAcc?.name,
            destAccountName: targetAcc?.name,
            date: safeDate,
            description: desc || 'Transferencia entre cuentas'
        });
    } else {
        // Gasto o Ingreso
        addTransaction({
            type: mode,
            amount: numAmount,
            accountId,
            categoryId,
            description: desc,
            date: safeDate,
            isFixed,
            isSavings: mode === 'expense' && isSavings,
            didWithdraw: mode === 'expense' && isSavings ? didWithdraw : true 
        });
    }
    
    if (onClose) onClose();
  };

  const filteredCategories = categories.filter(c => mode === 'income' ? c.type === 'income' : c.type !== 'income');

  return (
    <div className="space-y-4 text-white">
      <Tabs value={mode} onValueChange={(v:any) => { setMode(v); setIsSavings(false); }} className="w-full">
        <TabsList className="grid w-full grid-cols-3 bg-zinc-800">
          <TabsTrigger value="income" className="data-[state=active]:bg-green-600">Ingreso</TabsTrigger>
          <TabsTrigger value="expense" className="data-[state=active]:bg-red-600">Gasto</TabsTrigger>
          <TabsTrigger value="transfer" className="data-[state=active]:bg-blue-600">Transferir</TabsTrigger>
        </TabsList>
      </Tabs>

      <form onSubmit={handleSubmit} className="space-y-4 mt-4">
        
        {/* LÓGICA DE AHORRO (Solo en Gasto) */}
        {mode === 'expense' && (
            <div className="bg-zinc-900 p-3 rounded-lg border border-zinc-800">
                <div className="flex items-center justify-between">
                    <Label className="cursor-pointer">¿Es un movimiento de Ahorro?</Label>
                    <Switch checked={isSavings} onCheckedChange={setIsSavings} className="data-[state=checked]:bg-blue-500"/>
                </div>
                
                {isSavings && (
                    <div className="mt-3 pt-3 border-t border-zinc-800 animate-in slide-in-from-top-2">
                        <Label className="block mb-2 text-yellow-500">¿Retirar el dinero de la cuenta?</Label>
                        <div className="flex gap-4">
                             <div 
                                onClick={() => setDidWithdraw(true)}
                                className={`flex-1 p-2 rounded text-center cursor-pointer border ${didWithdraw ? 'bg-red-900/50 border-red-500 text-white' : 'bg-zinc-800 border-transparent text-zinc-500'}`}
                             >
                                SI <span className="text-xs block">(Resta saldo)</span>
                             </div>
                             <div 
                                onClick={() => setDidWithdraw(false)}
                                className={`flex-1 p-2 rounded text-center cursor-pointer border ${!didWithdraw ? 'bg-blue-900/50 border-blue-500 text-white' : 'bg-zinc-800 border-transparent text-zinc-500'}`}
                             >
                                NO <span className="text-xs block">(Mantiene saldo)</span>
                             </div>
                        </div>
                        <p className="text-[10px] text-zinc-400 mt-2">
                            {didWithdraw 
                                ? "El dinero saldrá de tu cuenta y se sumará a tu portafolio de ahorros." 
                                : "El dinero SE QUEDA en tu cuenta, pero se marcará como ahorro en azul y sumará a tu portafolio."}
                        </p>
                    </div>
                )}
            </div>
        )}

        <div className="space-y-1">
            <Label>Fecha</Label>
            <Input type="date" value={date} onChange={e => setDate(e.target.value)} className="bg-black border-zinc-700" />
        </div>

        <div className="space-y-2">
            <Label className="text-xl font-bold">Monto</Label>
            <Input 
                type="number" step="0.01" placeholder="$0" required autoFocus
                className="text-2xl font-bold bg-black border-zinc-700 h-14"
                value={amount} onChange={e => setAmount(e.target.value)}
            />
        </div>

        {/* CAMPOS SEGÚN MODO */}
        {mode === 'transfer' ? (
            <div className="grid grid-cols-2 gap-4 bg-zinc-900 p-3 rounded-lg">
                <div className="space-y-2">
                    <Label>Desde (Origen)</Label>
                    <Select onValueChange={setAccountId} required>
                        <SelectTrigger className="bg-zinc-800 border-zinc-700"><SelectValue placeholder="Cuenta" /></SelectTrigger>
                        <SelectContent>
                            {accounts.filter(a => !a.isHidden).map(acc => (
                                <SelectItem key={acc.id} value={acc.id}>{acc.name}</SelectItem>
                            ))}
                        </SelectContent>
                    </Select>
                </div>
                <div className="flex items-center justify-center pt-6">
                    <ArrowRightLeft className="text-zinc-500" />
                </div>
                <div className="space-y-2 col-span-2">
                    <Label>Hacia (Destino)</Label>
                    <Select onValueChange={setTargetAccountId} required>
                        <SelectTrigger className="bg-zinc-800 border-zinc-700"><SelectValue placeholder="Cuenta Destino" /></SelectTrigger>
                        <SelectContent>
                            {accounts.filter(a => !a.isHidden && a.id !== accountId).map(acc => (
                                <SelectItem key={acc.id} value={acc.id}>{acc.name}</SelectItem>
                            ))}
                        </SelectContent>
                    </Select>
                </div>
            </div>
        ) : (
            <div className="grid grid-cols-2 gap-4">
                 <div className="space-y-2">
                    <Label>Cuenta</Label>
                    <Select onValueChange={setAccountId} required>
                        <SelectTrigger className="bg-zinc-800 border-zinc-700"><SelectValue placeholder="Seleccionar" /></SelectTrigger>
                        <SelectContent>
                            {accounts.filter(a => !a.isHidden).map(acc => (
                                <SelectItem key={acc.id} value={acc.id}>{acc.name}</SelectItem>
                            ))}
                        </SelectContent>
                    </Select>
                </div>
                <div className="space-y-2">
                    <Label>Categoría</Label>
                    <Select onValueChange={setCategoryId} required>
                        <SelectTrigger className="bg-zinc-800 border-zinc-700"><SelectValue placeholder="Seleccionar" /></SelectTrigger>
                        <SelectContent>
                            {filteredCategories.map(cat => (
                                <SelectItem key={cat.id} value={cat.id}>{cat.name}</SelectItem>
                            ))}
                        </SelectContent>
                    </Select>
                </div>
            </div>
        )}

        <div className="space-y-2">
            <Label>Descripción / Notas</Label>
            <Input className="bg-zinc-800 border-zinc-700" value={desc} onChange={e => setDesc(e.target.value)} placeholder="Ej: Cena, Taxi, Ahorro viaje..." />
        </div>

        <Button type="submit" className={`w-full h-12 text-lg ${mode === 'income' ? 'bg-green-600' : mode === 'transfer' ? 'bg-blue-600' : 'bg-red-600'}`}>
          <Save className="mr-2 h-5 w-5" /> 
          {mode === 'income' ? 'Registrar Ingreso' : mode === 'transfer' ? 'Realizar Transferencia' : 'Registrar Gasto'}
        </Button>
      </form>
    </div>
  );
}